    <div class="section-line"></div>
    <p class="information section">

    Artists of all kinds — writers, musicians, filmmakers, painters — rely on the public domain: “Poetry can only be made out of other poems, novels out of other novels,” as the critic Northrop Frye put it. <br><br>Creators draw on previous works, and on the cultural artifacts around them; they remix vintage footage with new clips, turn books into plays and musicals, borrow lyrics and melodies from old songs, adapt classic stories to present day circumstances. For example, in the past year, you may have enjoyed the movies Gnomeo & Juliet, Red Riding Hood, or The Three Musketeers — these 2011 films reinterpreted public domain works by Shakespeare, Charles Perrault and the Brothers Grimm, and Alexandre Dumas. Or your children may have been transfixed again by Disney’s beloved Snow White, Pinocchio, and The Little Mermaid, which are based on public domain works by The Brothers Grimm, Carlo Collodi, and Hans Christian Anderson. <br><br> As Judge Richard Posner observed, if the underlying works were copyrighted, “Measure for Measure would infringe Promos and Cassandra, Ragtime would infringe Michael Kohlhaas, and Romeo and Juliet itself would have infringed Arthur Brooke’s The Tragicall Historye of Romeo and Juliet . . . which in turn would have infringed several earlier Romeo and Juliets, all of which probably would have infringed Ovid’s story of Pyramus and Thisbe.” The creative fruits of the public domain are all around us.    </p>
    
